img {
    width: 50px;
}

@font-face {
    font-family: 'Abadi Extra Light';
    src: url(components/fonts/Abadi.ttf);
}

.nav-link {
    font-family: Abadi Extra Light;
    color: rgb(124,124,124);
    display: inline-block;
    margin: 10px 2em 0 2em;
}

.nav-link.a {
    color: red;
}

.linked {
    font-family: Abadi Extra Light;
    color: rgb(124,124,124);
    display: inline-block;
    margin: 10px 2em 0 2em;
}

.insta {
    position: absolute;
    right: 10px;
}

.header {
    background-color: rgb(243,214,191);
    height: 80px;
    border-bottom: 1px solid black;
  }




  

  
  li {
      transition-duration: 1s;
  }
  
  ul li ul {
      background: orange;
      visibility: hidden;
    opacity: 0;
    min-width: 5rem;
      position: absolute;
    transition: all 0.5s ease;
      left: 0;
    display: none;
  }
  
  ul li:hover{
      background-color: blue;
  }

  ul li:hover > ul,
  ul li ul:hover {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  
  ul li ul li {
      clear: both;
      width: 100%;
  }